.advantage-item {
  display: flex;
  flex-direction: column;

  &--icon {
    display: flex;
    flex-direction: row;

    @media (min-width: @tablet-width) {
      text-align: center;
      display: flex;
      flex-direction: column;
    }
  }
}

.advantage-item--numbers {
  border-bottom: 1px solid #d6d6d6;

  @media (min-width: @tablet-width) {
    border-bottom: none;
  }
}


.advantage-item-wrapper {
  padding-top: 32px;
  padding-left: 5px;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding-top: 155px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 0;
  }
}

.advantage-item__title {
  .title ();
  padding: 66px 18px 11px 18px;
  .advantage-item ();

  span {
    display: contents;
  }

  @media (min-width: @tablet-width) {
    padding: 44px 36px 11px 36px;
    min-width: 180px;
    margin: 0;
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;

    span::after {
      content: "\A";
      white-space: normal;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 52px 50px 11px 50px;

    span::after {
      content: "\A";
      white-space: normal;
    }
  }

  &--icon {
    margin: 0;
    padding: 0;
    display: block;

    @media (min-width: @desktop-width) {
      padding: 25px 90px 11px 86px;
    }
  }

  &--photo {
    padding: 34px 18px 11px 18px;
    .title ();

    @media (min-width: @tablet-width) {
      min-width: 100px;
      padding: 54px 50px 11px 50px;
    }
  }

  &--numbers {
    padding: 20px 18px 11px 18px;

    span {
      display: none;
    }

    @media (min-width: @tablet-width) {
      min-width: 100px;
      padding: 44px 50px 11px 50px;
    }

    @media (min-width: @desktop-width) {
      padding: 60px 140px 11px 140px;
    }

    span::after {
      content: "\A";
      white-space: normal;
    }
  }
}

.advantage-item__title::after {
  content: attr(data-value);
  position: relative;
  top: 10px;
  left: 0;
  font-weight: 400;
  font-size: 16px;
  height: 21px;
}

.advantage-item__description {
  padding: 19px 18px 40px 18px;
  .description ();
  .advantage-item ();

  @media (min-width: @tablet-width) {
    min-width: 190px;
    padding: 25px 30px 56px 30px;
  }

  &--icon {
    padding: 16px 18px 0 0;

    span {
      display: contents;
    }

    @media (min-width: @tablet-width) {
      min-width: 154px;
      padding: 20px 42px 0 42px;

      span::after {
        content: "\A";
        white-space: normal;
      }
    }

    @media (min-width: @desktop-width) {
      padding: 12px 50px 41px 50px;
    }
  }

  &--numbers {
    padding: 16px 32px 30px 32px;

    span {
      display: contents;
    }

    @media (min-width: @tablet-width) {
      padding: 20px 32px 28px 32px;

      span::after {
        content: "\A";
        white-space: normal;
      }
    }

    @media (min-width: @desktop-width) {
      padding: 25px 50px 41px 50px;
    }
  }
}

.advantage-item__icon {
  position: relative;
  top: 25px;
  margin: 0;
  min-width: 40px;

  @media (min-width: @tablet-width) {
    position: absolute;
    padding: 40px 90px;
    width: 77px;
    height: 72px;
    top: 1050px;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    top: 25px;
    width: 80px;
    margin-left: 70px;
  }
}

.advantage-item:nth-of-type(3) .advantage-item__icon {
  @media (min-width: @desktop-width) {
    width: 100px;
  }
}
